    --main-para-color: white; */
    
    --main-bg-color: #fffafa;
    --alt1-bg-color: #ffeeee;
    --alt2-bg-color: #ffdfdf;
    --main-style-color: #bfedf5;
    --alt1-style-color: #f5b27d;
    --alt2-style-color: #96f5cd;
    --main-para-color: #2c2c2c;
}

body {
    background-color: var(--main-bg-color);
}

.App {
    text-align: center;
}

.App-header {
    background-color: var(--main-bg-color);
    height: 100%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;
    font-size: calc(10px + 2vmin);
    color: var(--main-para-color);
}

.Page-link {
    text-align: center;
    width: max(12.5%, fit-content);
    font-size: 3vmin;
    color: var(--alt1-style-color);
}

.Page-link:active {
    color: var(--alt1-style-color);
}

.Page-title {
    font-size: 2em;
    color: var(--alt2-style-color);
}

.Title-fake-page-link{
    width: max-content;
    font-size: 3vw;
    text-align:center;
    margin-top: auto;
    margin-bottom: auto;
}

.Company-name-logo-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: fit-content;
}

.Logo-in-top-shit {
    margin-left: 2vw;
    margin-top: auto;
    margin-bottom: auto;

    width: 7vw;
}

.Top-shit {
    background-color: var(--alt1-bg-color);
    font-size: calc(10px + 2vmin);
    color: var(--main-para-color);
    width: 100%;
    position: fixed;
    display: flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-evenly;

    border-bottom: thick ridge var(--main-style-color);
}

.Paragraph-holder-desktop, .Paragraph-holder-phone {
    width: 100%;
    height: 50vh;

    margin: auto;

    display: flex;

    margin-top: 10vh;
    margin-bottom: 10vh;

    background: var(--main-style-color);

    border-bottom: thick solid var(--alt1-style-color);
    border-top: thick solid var(--alt1-style-color);
}

.Paragraph-holder-desktop {
    flex-direction: row;
    align-content: center;
    justify-content: space-evenly;
}

.Paragraph-holder-phone {
    flex-direction: column;
    align-content: center;
    justify-content: space-evenly;
}

.Paragraph-title-phone, .Paragraph-title-desktop {
    color: var(--alt1-style-color);
    font-weight: 700;
    text-align: center;

    height:fit-content;
}

.Paragraph-title-phone {
    max-width: 60vw;
    
    font-size: max(8vw, 1em);

    margin-bottom: 0;
    padding-bottom: 0;
    
    margin-left: auto;
    margin-right: auto;
}

.Paragraph-title-desktop {
    max-width: 20vw;
    font-size: max(3vw, 1em);

    margin-top: auto;
    margin-bottom: auto;
}

.Paragraph-desktop, .Paragraph-phone {
    font-size: min(max(2.8vw, .5em), calc(10px + 2vmin));

    height:fit-content;
}

.Paragraph-phone {
    margin-left: auto;
    margin-right: auto;
    width:80%;
}

.Paragraph-desktop {
    margin-top: auto;
    margin-bottom: auto;
    width:60%;
}

.Spacer {
    width: 100vw;
    margin: 0;
    height: 12vh;
    left: 0;
    top: 0;
}
